import { Component, OnInit } from '@angular/core';

import { Restaurant } from '../restaurant';
import { RestaurantService } from '../restaurant.service';
import { VoteService } from '../vote.service';

@Component({
  selector: 'app-restaurant-list',
  templateUrl: './restaurant-list.component.html',
  styleUrls: ['./restaurant-list.component.css'],
})
export class RestaurantListComponent implements OnInit {
  restaurants: Restaurant[];
  restaurant: Restaurant;

  constructor(private restaurantService: RestaurantService, private voteService: VoteService) {
    this.restaurant = new Restaurant();
  };

  add(): void {
    console.log(this.restaurant);
    this.restaurantService.addRestaurant(this.restaurant).subscribe(result => this.restaurant = result);
    this.restaurants.push(this.restaurant);
    console.log('button add');
  }

  vote(restaurantReference: string): void {
    this.voteService.vote(restaurantReference).subscribe(response => console.log(response));
    console.log('vote Ok !');
  }

  ngOnInit() {
    this.restaurantService.getRestaurants().subscribe(restaurants => this.restaurants = restaurants);
  }

}



